import { Cell } from "fixed-data-table-2";
import { RRCProps, ReduxReusableComponents } from "../../reduxReusableComponents/ReduxReusableComponents";
import { Tree, PropsNotFromState as TreeProps, TreeReducers, TreeState } from "../TreeRRC/Tree";

export type PropsNotFromState = {
};

type TreeFixedDataTableProps = PropsNotFromState & TreeProps & RRCProps<TreeFixedDataTableState, TreeFixedDataTableReducers>;

export class TreeFixedDataTableState extends TreeState { }
export class TreeFixedDataTableReducers<S extends TreeFixedDataTableState = TreeFixedDataTableState> extends TreeReducers<S> { }

/**
 * The initial prototype had more code. But we improved the architecture, and some code disappeared. And we moved some more
 * code to `Tree`, and again some more code disappeared. So currently the class is pretty slim. But we keep it. Maybe in the 
 * future we'll add more TreeFDTable specific things.
 */
export class TreeFixedDataTable<T extends TreeFixedDataTableProps = TreeFixedDataTableProps> extends Tree<T> {

    protected renderItemWrapperInternal(props: any, ...children: any) {
        return <Cell {...props}>{children}</Cell>
    }

}

export const TreeFixedDataTableRRC = ReduxReusableComponents.connectRRC(TreeFixedDataTableState, TreeFixedDataTableReducers, TreeFixedDataTable);
